.skyplus-chip {
  font-size: rem(12px);
  line-height: 1.5;
  font-weight: 500;
  cursor: pointer;
  display: inline;
  font-family: $font-family-default;
  display: inline-block;

  &-container {
    white-space: pre;
    border-radius: rem(18px);
  }

  &-filled {
    padding: 4px 12px;
    border-radius: rem(18px);
  }

  &-no-fill {
    padding: 4px 12px;
    border: 0 !important;
  }

  &-primary {
    background-color: get-color(color-primary-main);
    color: get-color(color-action-active-background);
  }

  &-info {
    background-color: get-color(color-system-informaton-light);
    color: get-color(text-color-info);
  }

  &-white {
    background-color: get-color(color-secondary-white);
    color: get-color(color-text-secondary);
  }

  &-info-light-border {
    border: get-color(border-secondary);
  }

  &-sm {
    padding: rem(4px) rem(12px);
  }

  &-md {
    padding: rem(8px) rem(12px);
  }
}
