:root {
  --color-primary-main: #000099;
  --color-secondary-main: #afe4ff;
  --color-secondary-light: #eaf8ff;
  --color-secondary-medium: #d1efff;
  --color-secondary-deep-grey: #e2ebf2;
  --color-secondary-bright: #00aee5;
  --color-secondary-white: #ffffff;
  --color-accent-main: #7fd287;
  --color-accent-light: #f1fbf5;
  --color-accent-dark: #209326;
  --color-text-primary: #25304b;
  --color-text-secondary: #4b5772;
  --color-text-tertiary: #7a85a0;
  --color-text-disabled: #9ba4b8;
  --color-action-active: #25304b;
  --color-action-active-stroke: #9cd9ff;
  --color-action-active-background: #ffffff;
  --color-action-selected: #000099;
  --color-action-selected-stroke: #9cd9ff;
  --color-action-selected-background: #eaf8ff;
  --color-action-selected-disabled: #9ba4b8;
  --color-action-selected-disabled-stroke: #d7e1e9;
  --color-action-selected-disabled-background: #ffffff;
  --color-action-selected-disabled-focus: #000099;
  --color-button-active: #ffffff;
  --color-button-active-background: #000099;
  --color-button-disabled: #ffffff;
  --color-button-disabled-background: #bcc2cf;
  --color-button-disabled-background-light: #ebecee;
  --color-button-link: #000099;
  --color-gradient-primary: linear-gradient(180deg, #009 0%, #00005c 100%);
  --color-gradient-accent: linear-gradient(180deg, #e0fed7 0%, #c1edc1 100%);
  --color-gradient-stroke: linear-gradient(180deg, #009 0%, #00aee5 100%);
  --color-gradient-secondary: linear-gradient(
    180deg,
    #eaf8ff -58.33%,
    #afe4ff 156.94%
  );
  --color-system-error: #e4454d;
  --color-system-error-light: #f4dedf;
  --color-system-warning: #a97d0e;
  --color-system-warning-light: #fff8e5;
  --color-system-warning-bright: #ffbd12;
  --color-system-success: #218946;
  --color-system-success-light: #ddf5e6;
  --color-system-information: #348dc4;
  --color-system-informaton-light: #eaf8ff;
  --color-selection-control-active-text: #25304b;
  --color-selection-control-active-stroke: #000099;
  --color-selection-control-active-background: #ffffff;
  --color-selection-control-selected-text: #25304b;
  --color-selection-control-selected-background: #000099;
  --color-selection-control-disabled-text: #9ba4b8;
  --color-selection-control-disabled-background: #ffffff;
  --color-selection-control-toggle-active: #9ba4b8;
  --color-selection-control-toggle-selected: #000099;
  --border-secondary: 1px solid #daf1ff;
  --border-primary: 1px solid #daf1ff;
}

@function get-color($key: 'primary') {
  @return var(--#{$key});
}

@function get-opaque-color($key: 'primary', $opacity: '1') {
  $result: map-get($color-palette, $key);
  @return rgba($result, $opacity);
}
